.BarChart {

}

.BarChart .tick > path {
  stroke: lightgray;
}

.BarChart line.axis-baseline {
  stroke: none;
}

.BarChart .ordinal-labels text {
  /* vertical-align: top; */
  font-size: 0.8rem;
}

.BarChart .axis-labels text {
  font-size: 0.8rem;
}

.BarChart .tooltip-content {
  font-size: 0.7rem;
  background: #222222dd;
  color: white;
  border-radius: 2px;
  padding: 8px;
  z-index: 99;
  line-height: 1;
  /* font-weight: 700; */
  pointer-events: none;
  min-width: 60px;
  /* position: absolute; */
  top: -1rem;
  position: relative;
  left: 0.4rem;
}

.BarChart .tooltip-content:after {
  /* box-sizing: border-box;
  display: inline;
  font-size: 10px;
  width: 100%;
  line-height: 1;
  color: #222222dd;
  position: absolute;
  pointer-events: none; */
}

.Legend {
	position: absolute;
	bottom: 1em;
	left: 0.8em;
	z-index: 5000;
	margin: 8px;
	font-size: 0.8em;
	background-color: white;
	padding: 4px;
	opacity: 0.9;
	border: 1px solid #bbb;
}

.MapContainer {
  /* margin: 24px auto; */
  position: relative;
  border-right: 1px solid #d4d4d5;
  border-left: 1px solid #d4d4d5;
  border-bottom: 1px solid #d4d4d5;
  font-family: 'Roboto Condensed', 'sans-serif';
}

.MapContainer .leaflet-container {
  width: 100%;
  height: 400px;
}

.MapContainer .custom-tip {
  background-color: #333;
  color: white;
  border-color: #333;
  opacity: 0.85;
  font-size: 0.8em;
  font-family: 'Barlow', sans-serif;
  padding: 3px;
  font-weight: 400;
}

.MapContainer .custom-tip::before {
  border-top-color: #333;
}

.MapContainer div.leaflet-control-layers.leaflet-control {
	visibility: hidden;
}

